.main-nav {
  background-color: rgba(@color-black, 0.3);

  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 30.5px @desktop-padding;
  }

  &--opened {
    background-color: @color-blue;

    @media (min-width: @desktop-width) {
      background-color: rgba(@color-black, 0.3);
    }
  }
}

.main-nav__bar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 21px @mobile-padding;

  @media (min-width: @tablet-width) {
    padding-top: 43px;
    padding-bottom: 43px;
  }

  @media (min-width: @desktop-width) {
    width: 147px;
    padding: 0;
  }
}

.main-nav--opened .main-nav__bar {
  box-shadow: 0 1px 0 @color-black;

  @media (max-width: (@mobile-width-only)) {
    padding-top: 23px;
    padding-bottom: 23px;
  }

  @media (min-width: @desktop-width) {
    box-shadow: none;
  }
}

.main-nav__logo {
  width: 74px;
  height: 23px;

  cursor: pointer;

  &:hover svg {
    fill: @color-pink;
  }

  &:active svg {
    fill: @color-white;
    opacity: 0.3;
  }
}

.main-nav__toggle {
  .button();

  position: relative;

  width: 50px;
  height: 24px;

  &:hover svg {
    fill: @color-pink;
  }

  &:active svg {
    fill: @color-white;
    opacity: 0.3;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  width: 23px;
  height: 23px;

  @media (max-width: (@mobile-width-only)) {
    margin-right: 3px;
  }
}

.main-nav--opened .main-nav__toggle-icon--open {
  display: none;
}

.main-nav--closed .main-nav__toggle-icon--close {
  display: none;
}

.main-nav__menu {
  @media (min-width: @desktop-width) {
    width: 620px;
  }
}

.main-nav--opened .main-nav__menu {
  display: block;
}

.main-nav--closed .main-nav__menu {
  @media (max-width: (@desktop-width - 1)) {
    display: none;
  }
}

.main-nav__list {
  .list();

  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
  }
}

.main-nav__item {
  box-shadow: 0 1px 0 @color-black,
    inset 0 1px 0 rgba(@color-white, 0.15);

  @media (min-width: @desktop-width) {
    box-shadow: none;

    &--current {
      box-shadow: 0 2px 0 @color-white;
    }
  }
}

.main-nav__link {
  .button();
  padding: 15.5px 0;

  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;

  @media (min-width: @desktop-width) {
    font-weight: 400;
    line-height: 18px;
    text-transform: capitalize;
  }

  &:hover {
    color: @color-pink;
  }

  &:active {
    color: @color-white;
    opacity: 0.3;
  }
}
